* {
  color: #0376a8;
  font-family: 'Quicksand', sans-serif;
}

html,
body {
  margin: 0;
  padding: 0;
}

body {
  background: #f2e4ca;
}

.game {
  display: -webkit-flex;
  display: flex;
  padding: 24px 48px;
}

.game .game-title {
  text-align: center;
  margin-bottom: 16px;
}

.game .game-title,
.game .game-status {
  font-size: 24px;
}

.game .game-board {
  -webkit-flex: 1 1;
          flex: 1 1;
}

@media (max-width: 768px) {
  .game {
    -webkit-flex-direction: column;
            flex-direction: column;
  }

  .game .game-board {
    margin-bottom: 32px;
  }
}

.board {
  width: 388px;
  display: -webkit-flex;
  display: flex;
  margin: 0 auto;
  -webkit-flex-direction: column;
          flex-direction: column;
}

.board .board-row {
  display: -webkit-flex;
  display: flex;
  border-top: 4px solid #0169a5;
}

.board .board-row:last-child {
  border-bottom: 4px solid #0169a5;
}

@media (max-width: 500px) {
  .board {
    width: 288px;
  }
}

@media (max-width: 380px) {
  .board {
    width: 208px;
  }
}

/* reset defaults */
.square {
  margin: 0;
  padding: 0;
  border: none;
  outline: none;
}

.square {
  -webkit-flex: 1 1;
          flex: 1 1;
  width: 128px;
  height: 128px;
  font-size: 64px;
  color: #c260a8;
  font-weight: bold;
  text-align: center;
  background: transparent;
  border-left: 4px solid #0169a5;
}

.square:last-child {
  border-right: 4px solid #0169a5;
}

@media (max-width: 500px) {
  .square {
    width: 96px;
    height: 96px;
  }
}

@media (max-width: 380px) {
  .square {
    width: 64px;
    height: 64px;
    font-size: 48px;
  }
}

.move-history {
  border-radius: 3px;
  padding: 8px 8px 8px 24px;
  border: 1px dashed slategray;
}

.move-history .history-btn {
  width: 100%;
  outline: none;
  font-size: 18px;
  min-width: 190px;
  margin-bottom: 8px;
  background: transparent;
  border: 4px solid #0169a5;
  border-bottom: 8px solid #0169a5;
}

.move-history .history-btn:hover {
  cursor: pointer;
  font-weight: bold;
  border-color: #0b6498;
}

.move-history .history-btn.active {
  color: #c260a8;
  font-weight: bold;
  border-color: #c260a8;
}

